.success {
    width: 100%;
    color: $color-1;
    margin: auto;
    text-align: center;
    min-height: 100vh;
    border: 2px solid $color-1;
    display: flex;
    justify-content: center;
    align-items: center;

    .box {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 2rem;
        width: 50%;
        min-height: 50vh;
        background-color: $color-1;
        color: $color-3;

        @media(max-width: 900px) {
            width: 80%;
        }

        .entete {
            width: 50%;
            border-bottom: 1px solid $color-3;

            .icone {
                font-size: 3rem;
                color: #00FF61;
            }
        }

        .bas {
            display: flex;
            flex-direction: column;
            gap: 1rem;
            .check {
                color: #00FF61;
            }
        }
    }
}